@import '../../styles/shared.scss';

.todo-form {
  width: 90%;

  background-color: transparent;

  border-radius: 15px;

  &__section {
    padding: 24px 0;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    margin: 0 auto;
  }

  &__field {
    position: relative;

    width: 100%;
  }

  &__label,
  &__button {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

  &__input {
    @extend %input;
  }
}

.error-message {
  @extend %error;
  font-size: 14px;
}

.add-button {
  @extend %button;
  width: 50px;
  height: 50px;

  @include themify($themes) {
    color: themed('primary-light-white');
    background-color: themed('light-green');

    border: 1px solid themed('light-green');
  }
  border-radius: 100px;

  transition: color 250ms $timing-function,
    background-color 250ms $timing-function, border-color 250ms $timing-function;

  &:focus,
  &:hover {
    @include themify($themes) {
      background-color: themed('dark-accent');

      border-color: themed('dark-accent');
    }
  }
}
